export default class Question {
  constructor({ question, choices = {} }) {
    this.picked = null;
    this.question = question;
    this.choices = choices;
  }

  resolve(pick) {
    console.log('this.choices[pick]', this.choices[pick]);
    return this.choices[pick].resolve();
  }

  answer(pick) {
    this.picked = pick;
  }

  toString() {}

  setChoices(choices) {
    this.choices = choices;
    return this;
  }
}
