export class Choice {
  constructor(display, value, resolve = () => {}) {
    this.display = display;
    this.value = value;
    this.resolve = resolve;
  }
}

export const yesMaker = resolve => {
  return new Choice('Yes', 'yes', resolve);
};

export const noMaker = resolve => {
  return new Choice('No', 'no', resolve);
};
