<template>
  <div id="app">
    <div id="questionTree" class="quiz-card">
      <progress-bar />
      <div class="body">
        <div v-if="questionTree.visited.length === 1">
          <h3>Find the right grant opportunity for you</h3>
          <p>
            This resource can help you determine which AOA grants you are
            eligible for.
          </p>
        </div>
        <message
          :message="result"
          :is-success="isSuccess"
          v-if="!currentQuestion && message"
        />
        <quiz-question
          v-if="currentQuestion"
          :the-question="questionTree.currentQuestion"
          @selected="handleSelected"
        >
        </quiz-question>
        <div class="actions">
          <button
            class="btn btn-small btn-noborder"
            @click="handleBack"
            :style="backStyle"
          >
            Back
          </button>
          <button
            class="btn btn-small"
            type="button"
            @click="handleNext"
            :disabled="!selected"
            v-if="currentQuestion !== null"
          >
            Next
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QuizQuestion from './components/QuizQuestion.vue';
import Message from './components/Message.vue';
import ProgressBar from './components/ProgressBar.vue';
import {
  physicianSuccess,
  studentSuccess,
  residentSuccess,
  newInvestigator,
  sorry,
} from './data/result-messages';
import {
  question_1,
  first_year,
  affiliated,
  terminal_degree,
} from './data/questions';
import { Choice, yesMaker, noMaker } from './utils/choice';
import Question from './utils/question';
import QuestionTree from './utils/question-tree';

export default {
  name: 'App',
  components: {
    QuizQuestion,
    Message,
    ProgressBar,
  },

  created() {
    const q_question_1 = new Question({
      question: question_1.question,
      choices: {
        student: new Choice(
          'Osteopathic medical student',
          'student',
          () => 'studentQs.q_first_year'
        ),
        do: new Choice('DO in practice', 'do', () => 'doQs.q_first_year'),
        mdphd: new Choice(
          'MD in practice or PhD',
          'mdphd',
          () => 'mdphdQs.q_first_year'
        ),
        resident: new Choice(
          'Resident physician',
          'resident',
          () => 'residentQs.q_first_year'
        ),
      },
    });

    const q_first_year = first_year.question;

    const sorryFn = () => {
      return {
        target: null,
        payload: sorry,
      };
    };

    const q_affiliated = new Question({
      question: affiliated.question,
      choices: {
        yes: yesMaker(() => 'q_terminal_degree'),
        no: noMaker(sorryFn),
      },
    });

    const q_terminal_degree = new Question({
      question: terminal_degree.question,
      choices: {
        yes: yesMaker(() => {
          return { target: null, payload: physicianSuccess + newInvestigator };
        }),
        no: noMaker(() => {
          return { target: null, payload: physicianSuccess };
        }),
      },
    });

    const studentQs = new QuestionTree({
      id: 'student',
      initial: 'q_first_year',
      questions: {
        q_first_year: new Question({
          question: q_first_year,
          choices: {
            yes: yesMaker(sorryFn),
            no: noMaker(() => {
              return {
                target: null,
                payload: studentSuccess,
              };
            }),
          },
        }),
      },
    });

    const doQs = new QuestionTree({
      id: 'do',
      initial: 'q_first_year',
      questions: {
        q_first_year: new Question({
          question: q_first_year,
          choices: {
            yes: yesMaker(sorryFn),
            no: noMaker(() => 'q_terminal_degree'),
          },
        }),
        q_terminal_degree,
      },
    });

    const mdphdQs = new QuestionTree({
      id: 'mdphd',
      initial: 'q_first_year',
      questions: {
        q_first_year: new Question({
          question: q_first_year,
          choices: {
            yes: yesMaker(sorryFn),
            no: noMaker(() => 'q_affiliated'),
          },
        }),
        q_affiliated,
        q_terminal_degree,
      },
    });

    const residentQs = new QuestionTree({
      id: 'resident',
      initial: 'q_first_year',
      questions: {
        q_first_year: new Question({
          question: q_first_year,
          choices: {
            yes: yesMaker(sorryFn),
            no: noMaker(() => {
              return {
                target: null,
                payload: residentSuccess,
              };
            }),
          },
        }),
      },
    });

    this.questionTree = new QuestionTree({
      initial: 'q_question_1',
      questions: {
        q_question_1,
      },
      children: {
        studentQs,
        doQs,
        mdphdQs,
        residentQs,
      },
    });

    this.currentQuestion = this.questionTree.currentQuestion;
  },

  mounted() {},

  methods: {
    handleSelected(selected) {
      this.selected = selected;
    },

    handleNext() {
      const next = this.questionTree.next(this.selected);

      //this.currentQuestion = this.questionTree.next(this.selected);
      if (next.target !== null) {
        this.currentQuestion = next.target;
      } else {
        // TODO *HACK* for now to determine a negative outcome
        if (next.payload.indexOf('sorry') > 0) {
          this.isSuccess = false;
        }

        this.currentQuestion = next.target;
        this.result = next.payload;
      }

      this.selected = '';
    },

    handleBack() {
      this.currentQuestion = this.questionTree.prev();
      this.isSuccess = true;
      this.selected = '';
    },
  },

  computed: {
    canGoBack() {
      return this.questionTree.canGoBack();
    },

    backStyle() {
      return {
        visibility: this.questionTree.visited.length > 1 ? 'visible' : 'hidden',
      };
    },
  },

  data() {
    return {
      progress: 0,
      selected: '',
      nanobar: null,
      result: '',
      questionTree: null,
      currentQuestion: '',
      message: 'hiya',
      hasNext: true,
      isSuccess: true,
    };
  },
};
</script>

