export default class QuestionTree {
  constructor(options) {
    this.id = options.id || 'parent';
    this.questions = options.questions || {}; // TODO must include
    this.children = options.children || {};
    this.initial = options.initial || ''; // TODO must include

    this.visited = [];
    this.tree = this.questions;
    this.currentQuestion = this.tree[this.initial];
    this.responses = {};

    this.visited = this.registerVisit(this.initial);
  }

  registerVisit(target) {
    return this.visited.concat(target);
  }

  setCurrentQuestion() {}

  // TODO we'll use this to track progress %
  maximumNodesUntilLeaf(node) {}

  canGoBack() {
    return this.visited.length > 1;
  }

  next(pick) {
    this.responses[this.currentQuestion.question] = pick;

    let next = this.currentQuestion.resolve(pick);

    let target;

    if (typeof next === 'string' || next === null) {
      target = next;
    } else if (typeof next === 'object') {
      target = next.target;
    } else {
      throw new Error('Invalid resolve function');
    }

    this.visited = this.registerVisit(target);

    // how we designate a leaf --> { target: null }
    if (target === null) {
      const toReturn = Object.assign(
        {},
        { target: target },
        { payload: next.payload }
      );
      return toReturn;
    }

    const split = target.split('.');

    let currentQuestion;

    if (split.length > 1) {
      this.tree = this.children[split[0]];

      currentQuestion = this.tree.questions[split[1]];
    } else {
      currentQuestion = this.tree.questions[target];
    }

    this.currentQuestion = currentQuestion;

    return Object.assign({}, { target: this.currentQuestion });
  }

  /**
   * Start from the root and traverse according to this.visited
   * until we reach the target
   *
   */
  retraceUntil(target) {
    let nextQuestion;
    let tree = this;

    for (let q of this.visited) {
      const split = q.split('.');

      if (split.length > 1) {
        tree = this.children[split[0]];
        nextQuestion = tree.questions[split[1]];
      } else {
        nextQuestion = tree.questions[q];
      }

      if (q == target) {
        break;
      }
    }

    delete this.responses[nextQuestion.question];
    return nextQuestion;
  }

  prev() {
    const newVisited = this.visited.slice(0, this.visited.length - 1);

    // TODO handle newvisited length potential problems
    const prevQuestion = this.retraceUntil(newVisited[newVisited.length - 1]);

    console.log('prevQuestion', prevQuestion);
    if (prevQuestion) {
      this.visited = newVisited;
      this.currentQuestion = prevQuestion;

      return this.currentQuestion;
    }
  }
}
