<template>
  <div class="question">
    <div class="question__question">{{ theQuestion.question }}</div>
    <div
      class="question__answers"
      v-for="choice in theQuestion.choices"
      :key="choice.value"
    >
      <label :for="choice.value">
        <input
          type="radio"
          name="pick"
          :id="choice.value"
          :value="choice.value"
          @change="$emit('selected', $event.target.value)"
          v-model="picked"
        />
        {{ choice.display }}</label
      >
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: ['theQuestion'],

  watch: {
    theQuestion() {
      this.picked = '';
    },
  },

  data() {
    return {
      picked: '',
    };
  },
};
</script>

<style scoped></style>
